<script>
import Alert from '@/components/Alert.vue';
import Pagination from '@/components/Pagination.vue';
import Loading from '@/components/Loading.vue';
import emitter from '@/assets/js/emitter';
import 'bootstrap/js/dist/tab';

export default {
  components: {
    Alert,
    Pagination,
    Loading,
  },
  data() {
    return {
      // alert
      alertMessage: '',
      alertStatus: false,
      // 
      isLoading: false,
      // 
      productData: [],
      // 
      allProductsData: [],
      // 
      pagination: [],
      // /
      loginStatus: false,
      // 
      dataLength: 0,
      // 
      product: {},
      // 
      loadingStatue: {
        // 
        viewContentStatus: '',
        // 
        addCart: '',
      },
      // 
      productCategory: [],
      // 
      nowCategory: '',
      // 
      myFavorite: this.getLoCalStorage('myFavorite') || [],
    };
  },
  methods: {
    // 
    getProduct(page = 1) {
      this.getAllProducts();
      this.$http
        .get(
          `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/products?page=${page}`,
        )
        .then((res) => {
          if (res.data.success) {
            this.productData = res.data.products;
            this.pagination = res.data.pagination;
            // 
            const category = new Set([]);
            res.data.products.forEach((item) => {
              category.add(item.category);
            });
            this.productCategory = [...category];
            this.dataLength = this.productData.length;
            this.isLoading = false;
          } else {
            this.alertMessage = res.data.message;
            this.alertStatus = false;
            setTimeout(() => {
              this.alertMessage = '';
              this.alertStatus = false;
            }, 2000);

            this.$router.push('/Login');
          }
        })
        .catch((err) => {
          this.alertMessage = err.data.message;
          this.alertStatus = false;
          setTimeout(() => {
            this.alertMessage = '';
            this.alertStatus = false;
          }, 2000);
          this.isLoading = false;
        });
    },
    // 
    getAllProducts() {
      this.$http
        .get(
          `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/products/all`,
        )
        .then((res) => {
          if (res.data.success) {
            this.allProductsData = res.data.products;
          } else {
            this.alertMessage = res.data.message;
            this.alertStatus = false;
            setTimeout(() => {
              this.alertMessage = '';
              this.alertStatus = false;
            }, 2000);
          }
        })
        .catch((err) => {
          this.alertMessage = err.data.message;
          this.alertStatus = false;
          setTimeout(() => {
            this.alertMessage = '';
            this.alertStatus = false;
          }, 2000);
        });
    },
    // 
    addCart(id, qty = 1) {
      this.loadingStatue.addCart = id;
      const product = {
        data: {
          product_id: id,
          qty: Number(qty),
        },
      };
      this.$http
        .post(
          `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/cart`,
          product,
        )
        .then((res) => {
          if (res.data.success) {
            this.loadingStatue.addCart = '';
            this.alertMessage = `${res.data.message}!`;
            this.alertStatus = true;
            setTimeout(() => {
              this.alertMessage = '';
              this.alertStatus = false;
            }, 1000);
            // ()
            emitter.emit('refresh-carts');
          } else {
            this.alertMessage = `${res.data.message}!`;
            this.alertStatus = false;
            setTimeout(() => {
              this.alertMessage = '';
              this.alertStatus = false;
            }, 1000);
          }
        })
        .catch((err) => {
          this.alertMessage = err.data.message;
          this.alertStatus = false;
          setTimeout(() => {
            this.alertMessage = '';
            this.alertStatus = false;
          }, 1000);
        });
    },
    // 
    addItemsToCart(item) {
      const product = {
        data: {
          product_id: item.id,
          qty: item.qty,
        },
      };
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/cart`;
      this.$http
        .post(url, product)
        .then((res) => {
          if (res.data.success) {
            this.alertMessage = `${res.data.message}!`;
            this.alertStatus = true;
            setTimeout(() => {
              this.alertMessage = '';
              this.alertStatus = false;
            }, 2000);
            // ()
            emitter.emit('refreshCarts');
          } else {
            this.alertMessage = `${res.data.message}!`;
            this.alertStatus = false;
            setTimeout(() => {
              this.alertMessage = '';
              this.alertStatus = false;
            }, 2000);
          }
        })
        .catch((err) => {
          this.alertMessage = err.data.message;
          this.alertStatus = false;
          setTimeout(() => {
            this.alertMessage = '';
            this.alertStatus = false;
          }, 2000);
        });
    },
    // 
    viewOneProduct(item) {
      this.$router.push(`/product/${item.id}`);
    },
    // 
    addFavorite(id) {
      if (this.myFavorite.includes(id)) {
        this.myFavorite.splice(this.myFavorite.indexOf(id), 1);
      } else {
        this.myFavorite.push(id);
      }

      this.setLoCalStorage('myFavorite', this.myFavorite);
      // ()
      emitter.emit('refresh-favorites');
    },
    // loCalStorage
    setLoCalStorage(name, item) {
      localStorage.setItem(name, JSON.stringify(item));
    },
    // loCalStorage
    getLoCalStorage(name) {
      return JSON.parse(localStorage.getItem(name));
    },
  },
  computed: {
    filterProductCategory() {
      return this.allProductsData.filter((item) => item.category.match(this.nowCategory));
    },
  },
  mounted() {
    this.isLoading = true;
    this.getProduct();
  },
};
</script>

